#monthsFrieze
{
    display: block;
    margin: 0 auto;
}

#monthEvnts
{
    display: grid;
    grid-template-areas:
            "a a d b b"
            "a a d b b"
            "a a d b b";
    grid-gap: 1vmax;
    justify-content: center;
    margin: 0 auto;
}

#listMonthEvntsDiv
{
    grid-area: b;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

#monthTitle
{
    font-family: "Kaushan Script", cursive;
    color: white;
    text-shadow: 0 0 1vmax black;
}

#currentMonthIMG
{
    grid-area: a;
    display: block;
    margin: auto;
}

.liEVNT
{
    text-align: left;
    margin: 0;
}

#listMonthEvnts
{
    color: white;
    margin: 0 auto;
    text-shadow: 0 0 1vmax black;
    font-family: Roboto,sans-serif;
}


#traitVertical
{
    grid-area: d;
    display: block;
    margin: auto 0;
}

.monthsBTN{
    cursor: pointer;
}

@media only screen and (max-width: 767px) {
    #monthEvnts
    {
        width: 90vw;
        height: 22vh;
        transform: scale(0.9);
    }

    #traitVertical
    {
        height: 20vh;
    }

    #currentMonthIMG
    {
        height: 22vh;
    }

    .monthsBTN
    {
        height: 12vh;
        position: absolute;
    }

    #monthsFrieze
    {
        width: 90vw;
        height: 52vh;
        position: relative;
    }

    #listMonthEvnts
    {
        font-size: 1.3vmax;
    }

    #monthTitle
    {
        font-size: 3vmax;
    }

    #listMonthEvntsDiv
    {
        width: 30vw;
        height: 22vh;
    }

    #Sept
    {
        top: 39.6vh;
        left: calc(45vw - 6vh);
    }

    #Oct
    {
        top: 34.4vh;
        left: calc(45vw - 16vh);
    }

    #Nov
    {
        top: 34.4vh;
        left: calc(45vw + 4vh);
    }

    #Déc
    {
        top: 27.2vh;
        left: calc(45vw - 6vh);
    }

    #Jan
    {
        top: 22vh;
        left: calc(45vw - 16vh);
    }

    #Fév
    {
        top: 22vh;
        left: calc(45vw + 4vh);
    }

    #Mars
    {
        top: 14.8vh;
        left: calc(45vw - 6vh);
    }

    #Avr
    {
        top: 9.6vh;
        left:calc(45vw - 16vh);
    }

    #Mai
    {
        top: 9.6vh;
        left:calc(45vw + 4vh);
    }

    #Juin
    {
        top: 2.4vh;
        left: calc(45vw - 6vh);
    }
}

@media only screen and (min-width: 768px) {
    #monthEvnts
    {
        width: 50vw;
    }

    #traitVertical
    {
        height: 15vw;
    }

    #listMonthEvntsDiv
    {
        width: 15vw;
    }

    #listMonthEvnts
    {
        font-size: 0.9vmax;
    }

    #monthTitle
    {
        font-size: 1.7vmax;
    }

    #currentMonthIMG
    {
        width: 15vw;
    }


    #monthsFrieze
    {
        width: 60vw;
        height: 40vh;
        position: relative;
    }

    .monthsBTN
    {
        width: 10vw;
        position: absolute;
    }

    #Sept
    {
        top: 3vh;
        left: 1vw;
    }

    #Oct
    {
        top: 8vw;
        left: 6vw;
    }

    #Nov
    {
        top:3vh;
        left:11vw;
    }

    #Déc
    {
        top: 8vw;
        left: 16vw;
    }

    #Jan
    {
        top: 3vh;
        left: 21vw;
    }

    #Fév
    {
        top: 8vw;
        left: 26vw;
    }

    #Mars
    {
        top: 3vh;
        left: 31vw;
    }

    #Avr
    {
        top: 8vw;
        left:36vw;
    }

    #Mai
    {
        top:3vh;
        left:41vw;
    }

    #Juin
    {
        top: 8vw;
        left: 46vw;
    }
}